export interface ElementInfo {

    label:string;

    value:string;

    container:HTMLDivElement;

    elementWrapper:HTMLDivElement;

    element:HTMLElement;

}

export interface WrapperInfo {

    container: HTMLDivElement;

    labelContainer:HTMLDivElement;

    inputContainer:HTMLDivElement;

}

export interface SelectOption {

    value:string;

    text:string;

    element?:HTMLOptionElement;

}

export class Form {

    static wrapper () : WrapperInfo {
        let wrapperInfo = {} as WrapperInfo;

        let container = document.createElement('div');
        let inputLabelWrapper = document.createElement('div');
        let inputWrapper = document.createElement('div');

        container.classList.add('form-group');

        inputLabelWrapper.classList.add('label');

        inputWrapper.classList.add('input-wrapper');

        container.appendChild(inputLabelWrapper);
        container.appendChild(inputWrapper);

        wrapperInfo.container = container;
        wrapperInfo.labelContainer = inputLabelWrapper;
        wrapperInfo.inputContainer = inputWrapper;

        return wrapperInfo;
    }

    static likeInput (label:string, value:string) : ElementInfo {
        let emptyWrapper = Form.wrapper();
        let elementInfo = {} as ElementInfo;

        let labelElement = document.createElement('label');
        let inputElement = document.createElement('span');
        emptyWrapper.labelContainer.appendChild(labelElement);
        emptyWrapper.inputContainer.appendChild(inputElement);
        labelElement.innerHTML = label;
        inputElement.innerHTML = value;

        elementInfo.container = emptyWrapper.container;
        elementInfo.element = inputElement;
        elementInfo.elementWrapper = emptyWrapper.inputContainer;
        elementInfo.label = label;
        elementInfo.value = value;

        return elementInfo;
    }

    static select (label:string, name:string, options:SelectOption[] = [], args:any = {}) : ElementInfo {
        let elementInfo = {} as ElementInfo;
        let wrapper = Form.wrapper();

        let labelElement:HTMLLabelElement = document.createElement('label');
        labelElement.innerHTML = label;

        let select:HTMLSelectElement = document.createElement('select');
        select.name = name;
        select.classList.add('input');

        for (let i = 0; i < options.length; i++) {
            let option = document.createElement('option');
            option.innerHTML = options[i].text;
            option.value = options[i].value;

            options[i].element = option;

            select.appendChild(option);
        }

        for (let key in args) {
            select.setAttribute(key, args[key]);
        }

        wrapper.inputContainer.appendChild(select);
        wrapper.labelContainer.appendChild(labelElement);

        elementInfo.container = wrapper.container;
        elementInfo.element = select;
        elementInfo.label = label;
        elementInfo.elementWrapper = wrapper.inputContainer;

        return elementInfo;
    }

    static text (label:string, name:string, attributes:any = {}, value:string = '') : ElementInfo {
        let elementInfo:ElementInfo = {} as ElementInfo;

        let container = document.createElement('div');
        let inputLabelWrapper = document.createElement('div');
        let inputLabel = document.createElement('label');
        let inputWrapper = document.createElement('div');
        let element = document.createElement('input');

        container.classList.add('form-group');

        inputLabelWrapper.classList.add('label');
        inputLabel.innerHTML = label;

        inputWrapper.classList.add('input-wrapper');
        element.classList.add('input');
        element.setAttribute('type', 'text');
        element.setAttribute('name', name);

        for (let key in attributes) {
            element.setAttribute(key, attributes[key]);
        }

        container.appendChild(inputLabelWrapper);
        container.appendChild(inputWrapper);

        inputLabelWrapper.appendChild(inputLabel);
        inputWrapper.appendChild(element);

        element.value = value;

        elementInfo.label = label;
        elementInfo.value = value;
        elementInfo.elementWrapper = inputWrapper;
        elementInfo.element = element;
        elementInfo.container = container;

        return elementInfo;
    }

    static number (label:string, name:string, attributes:any = {}, value:string = '') : ElementInfo {
        let info = Form.text(label, name, attributes, value);

        info.element.setAttribute('type', 'number');

        return info;
    }

    static hidden (name:string, value:string) : ElementInfo {
        let container = {} as ElementInfo;

        let input:HTMLInputElement = document.createElement('input');
        input.type = 'hidden';
        input.name = name;
        input.value = value;

        container.container = input as any;
        container.element = input;

        return container;
    }

    static checkbox (label:string, name:string, value:string, hideLabel:boolean = false) {
        let container:ElementInfo = {} as ElementInfo;

        let wrapper = document.createElement('div');
        let labelWrapper = document.createElement('div');
        let inputWrapper = document.createElement('div');

        wrapper.classList.add('form-group');
        labelWrapper.classList.add('label');
        inputWrapper.classList.add('input-wrapper');
        inputWrapper.classList.add('checkbox-group');

        container.container = wrapper;
        container.elementWrapper = inputWrapper;

        labelWrapper.innerHTML = label;

        wrapper.appendChild(labelWrapper);
        wrapper.appendChild(inputWrapper);

        let checkboxWrapper = document.createElement('label');
        let checkbox:HTMLInputElement = document.createElement('input');
        let checkboxInputWrapper = document.createElement('span');
        let inputLabel = document.createElement('span');

        container.element = checkbox;
        checkboxWrapper.appendChild(checkbox);
        checkboxWrapper.appendChild(checkboxInputWrapper);
        checkboxWrapper.appendChild(inputLabel);

        checkbox.name = name;
        checkbox.value = value;
        checkbox.type = 'checkbox';

        checkboxInputWrapper.classList.add('input-wrapper');
        inputLabel.classList.add('input-label');

        inputLabel.innerHTML = label;

        inputWrapper.appendChild(checkboxWrapper);

        if (hideLabel) {
            labelWrapper.classList.add('hide');
            wrapper.classList.add('has-offset');
            wrapper.classList.add('force');
        }

        return container;
    }

}
